*{
  margin-left: auto;
  margin-right: auto;
}





.heading{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding: 50px;
  
  margin-top: -2%;
  text-align: center;
  background: #1abc9c;
  color: white;
  font-size: 40px;
  text-transform: uppercase;
}





.card1 {
  
	display: inline-block;
  align-items: center;
	width: 30%;
  text-align: center;
  color: rgba(214,68,71,255);    
  background-color: #fff5f4;
	max-width: 10rem;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
	/* margin: 1rem  0rem  5rem  27rem ; */
  border-radius: 5px;
  margin-left: 29rem;
  margin-bottom: 2rem;
  margin-top: 2rem;
  margin-right: 0rem;
  position: relative;
	font-size: 1.5rem;
	text-decoration: none;
  padding: 18px 0px;
	overflow: hidden;
}
  @media only screen and (min-width: 375px) and (max-width: 812px) {
    .box{    
        padding-top: 60px;
        padding-bottom: 60px;
    }
  
  
  

} 

.card1:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }
.card_total1{
    display: block;
    font-size: 14px;
    background-color: #fdd6d7;
    color: #d06568;
    padding: 5px 0px;
    margin: 0px 0px -18px 0px;
    width: auto;
    border-radius: 0px;
    font-family: Arial, Helvetica, sans-serif;
    box-shadow: rgba(0,0,0,0.9);
    transition: all 200ms ease-in-out;
    text-decoration: none;
    
}

